.card {
  margin: 0 30px 25px 0;
  border: 1px solid #ebebeb;
  padding: 20px;
  width: 220px;
  border-radius: 40px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  &:hover {
    box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
    transform: translateY(-2px);
  }
  .card__favorite {
    position: absolute;
    cursor: pointer;
  }
  h5 {
    font-weight: 400;
    font-size: 14px;
  }
  span {
    opacity: 0.5;
    text-transform: uppercase;
    font-size: 14px;
  }
  b {
    font-size: 14px;
  }
  .plus {
    cursor: pointer;
  }
}
